import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { NegotiableBean } from './../bean/negotiable.bean';
// import { IsNegotiable, IsC } from './../../constant/codeValue';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { decimalInt, checkIntNum, specialSymbol, checkNumber } from 'app/@uisftech/common/service/validator';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-interest-negotiable-add',
  templateUrl: './interest-negotiable-add.component.html',
  styleUrls: ['./interest-negotiable-add.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class InterestNegotiableAddComponent implements OnInit {
  negotiableBean: NegotiableBean = new NegotiableBean();
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //提示信息
  msgs: Message[] = [];
  isNegotiable: any = [];
  isC: any = [];
  changeRed: boolean = false;
  isRed: boolean = false;
  show: boolean = false;
  showcode: boolean = false;
  userform: FormGroup; //校验
  taskCategoryId: string;
  decimalInt = decimalInt;
  checkIntNum = checkIntNum;
  isDisable: boolean = false;
  code: any;//获取所有码值

  showThis: boolean = true;
  mustShow: boolean = true;
  constructor(
    public httpService: ValuationHttpService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public fb: FormBuilder,
    public commfunc: Commfunc) {
    this.codeValues();
    this.isNegotiable = this.code['IsNegotiable'];
    this.isC = this.code['IsC'];
    //审核，工作流
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }
  ngOnInit() {
    this.negotiableBean = new NegotiableBean();
    this.negotiableBean.pageSize = 1;
    this.negotiableBean.pageNum = 10;
    this.userform = this.fb.group({
      'plnRsetName': new FormControl('', [Validators.required, Validators.maxLength(128), specialSymbol]),
      'plnRsetSet': new FormControl('', Validators.required),
      'plnRsetCode': new FormControl(''),
      'intFlotVal': new FormControl(''),
      'txInf': new FormControl('', Validators.maxLength(42)),
      'rateMax': new FormControl(''),
      'rateMin': new FormControl('')

    });

    this.negotiableBean.plnRsetCode = '';
    this.negotiableBean.intFlotVal = ''
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //做浮动值得校验
  checkIntFlotVal() {
    if (this.negotiableBean.intFlotVal == '') {
      return false;
    }

    if (this.negotiableBean.plnRsetCode == '2' || this.negotiableBean.plnRsetCode == '1' || this.negotiableBean.plnRsetSet == '4') {
      if (this.decimalInt(null, this.negotiableBean.intFlotVal)) {
        return true;
      }
      return false;
    }
    return false;
  }

  checkIntFlot() {
    if (this.negotiableBean.intFlotVal == '') {
      return false;
    }

    if (this.negotiableBean.plnRsetCode == '3') {

      if (this.checkIntNum(null, this.negotiableBean.intFlotVal)) {
        return true;
      }
      return false;
    }
    return false;
  }


  //做置灰
  disableText() {
    if (this.negotiableBean.plnRsetCode == "") {
      this.negotiableBean.intFlotVal = '';
      this.changeRed = false;
      return
    } else {
      this.changeRed = true;
      return
    }
  }
  disableText2() {
    if (this.negotiableBean.plnRsetSet == '1' || this.negotiableBean.plnRsetSet == '3') {
      this.show = true;
      this.showcode = true;
      this.negotiableBean['plnRsetCode'] = '';
      this.negotiableBean['intFlotVal'] = '';
      this.showThis = true;
      this.mustShow = true;
      this.negotiableBean.rateMax = '';
      this.negotiableBean.rateMin = '';
    } else if (this.negotiableBean.plnRsetSet == '2') {
      this.show = false;
      this.showcode = false;
      this.showThis = true;
      this.mustShow = true;
      this.negotiableBean.rateMax = '';
      this.negotiableBean.rateMin = '';
    } else if (this.negotiableBean.plnRsetSet == '4') {
      this.showThis = false;
      this.showcode = false;
      this.mustShow = false;
      this.show = true;
      this.negotiableBean.plnRsetCode = '';
    } else {
      this.showThis = true;
      this.showcode = true;
      this.mustShow = true;
      this.show = false;
      this.negotiableBean.rateMax = '';
      this.negotiableBean.rateMin = '';
    }
  }
  keyboard(event) {
    if (event.target.value.length > 0) {
      this.changeRed = false;
    } else {
      this.changeRed = true;
    }

  }

  //提交
  submit() {
    if (this.isDisable) {
      return;
    }
    if (this.checkIntFlotVal()) {
      return;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    };
    if (this.negotiableBean.plnRsetSet == '4' && (!this.negotiableBean.rateMin || this.negotiableBean.rateMin == '')) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "最低利率必输!" });
      return
    }
    if (this.negotiableBean.plnRsetSet == '4' && (!this.negotiableBean.rateMax || this.negotiableBean.rateMax == '')) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "最高利率必输!" });
      return
    }
    if (this.negotiableBean.plnRsetSet == '4' && this.negotiableBean.intFlotVal == '') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "协商浮动值必输!" });
      return
    }
    if (this.negotiableBean.plnRsetCode == '1' && (this.negotiableBean.intFlotVal == '' || this.negotiableBean.intFlotVal == undefined)) {
      return false
    }

    //可协商设定方式下拉控制
    if (this.negotiableBean.plnRsetSet == '2') {
      if (!this.negotiableBean.plnRsetCode) {
        this.isRed = true;
        return;
      }
    }

    // 校验协商设定方式 
    if (this.negotiableBean.plnRsetSet == '2' && this.negotiableBean.plnRsetCode == '0') {
      return
    }

    // 校验浮动值
    if ((this.negotiableBean.plnRsetCode == '2' || this.negotiableBean.plnRsetCode == '1') && this.decimalInt(null, this.negotiableBean.intFlotVal)) {
      return
    } else if (this.negotiableBean.plnRsetCode == '3' && this.checkIntNum(null, this.negotiableBean.intFlotVal)) {
      return
    }

    //校验浮动值为空
    if (this.negotiableBean.plnRsetSet == '2' && this.negotiableBean.intFlotVal == '' && this.negotiableBean.plnRsetCode != '0') {
      return
    }

    this.isDisable = true;
    // this.beanVariety.dmsnlChiNm = this.codeValue(this.beanVariety.dmsnlChiNm,this.list);
    //处理工作流的参数
    this.negotiableBean['taskCategoryId'] = this.taskCategoryId + '';
    this.httpService.addRateNegotionRuleService(this.negotiableBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        setTimeout(() => {
          window.history.go(-1);
        }, 1000)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //返回
  back() {
    window.history.go(-1);
  }
  //校验客户号只能输入数字
  onKeyupNumber(num) {
    if (num == 1) {
      if (this.negotiableBean.rateMin != '') {
        if (decimalInt(null, this.negotiableBean.rateMin)) {
          return true;
        }
        return false;
      }
    } else if (num == 2) {
      if (this.negotiableBean.rateMax != '') {
        if (decimalInt(null, this.negotiableBean.rateMax)) {
          return true;
        }
        return false;
      }
    }
  }
  checkNum() {
    if (this.negotiableBean.rateMin != '' && this.negotiableBean.rateMax != '') {
      if (Number(this.negotiableBean.rateMin) >= Number(this.negotiableBean.rateMax)) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "最低利率不能大于等于最高利率!" });
        return
      }
    }
  }
}
