import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { NegotiableBean } from './bean/negotiable.bean';
import { Router } from '@angular/router';
// import { IsRcord, IsC } from '../constant/codeValue';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
@Component({
  selector: 'app-interest-negotiable',
  templateUrl: './interest-negotiable.component.html',
  styleUrls: ['./interest-negotiable.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class InterestNegotiable implements OnInit {
  negotiableBean: NegotiableBean = new NegotiableBean();
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  total: number = 10;
  isRcod: any = [];
  isC: any = [];
  first: number = 0;//分页控制
  tabMesg: any;//表格中无数据时显示的内容
  code: any;//获取所有码值
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public commfunc: Commfunc) {
    this.codeValues();
    this.isRcod = this.code['IsRcord'];
    this.isC = this.code['IsC'];
  }

  ngOnInit() {
    this.negotiableBean.pageSize = 10;
    this.negotiableBean.pageNum = 1;
  }
   //码值
   codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  querySecle() {
    this.negotiableBean.pageSize = 10;
    this.negotiableBean.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
  toquery() {
    this.httpService.queryRateNegotionRuleService(this.negotiableBean).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      }
      this.list = data.resultList;
      this.total = data.total;

    }, err => {
      this.tabMesg = tableMessage
    })
  }
  //重置
  newSet() {
    this.negotiableBean = new NegotiableBean();
    this.negotiableBean.pageSize = 10;
    this.negotiableBean.pageNum = 1;
    // this.first = 0;
    // this.toquery();
  }
  //添加
  add(param) {
    this.headerTitle = '利率可协商-新增';
    this.router.navigate(['/pages/tzb/valuation/interest-negotiable/interest-negotiable-add'])

  }
  //新增的回调函数
  addCall(param) {
    this.display = param;
  }
  //详情
  detail(param) {
    this.headerTitle = '利率可协商-详情';
    this.showModel = '3';
    this.display = true;
    this.inValueCode = param.plnRsetNum;
  }
  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //更新
  update(param) {
    this.headerTitle = '利率可协商-修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param.plnRsetNum;
  }
  //修改的回调函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.toquery();
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.negotiableBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.negotiableBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.negotiableBean.pageSize;

    //调用查询的方法
    this.toquery();
    // this.negotiableBean.pageNum = 1;
  }
  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          plnRsetNum: car.plnRsetNum,
        };
        this.httpService.deleteRateNegotionRuleService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.toquery();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}


